s/defaults/right.png\" alt=\"\" />\r\n        </div>\r\n        <div style=\"padding-bottom: 20%\">\r\n            <div class=\"container about-form\">\r\n                <div style=\"width: 50%\" class=\"contact-form\">\r\n                    <h1>Get in touch</h1>\r\n                    <h3>we are here for you! How can we help?</h3>\r\n                    <form @submit.prevent=\"contactSubmit($event)\">\r\n                        <label for=\"name\">\r\n                            <input\r\n                                type=\"text\"\r\n                                id=\"name\"\r\n                                name=\"name\"\r\n                                v-model=\"contactForm.name\"\r\n                                placeholder=\"Enter Your Name\"\r\n                                required\r\n                            />\r\n                            <span v-if=\"errors && errors.name\" class=\"errMsg\">{{\r\n                                errors.name[0]\r\n                            }}</span>\r\n                        </label>\r\n                        <label for=\"email\">\r\n                            <input\r\n                                type=\"email\"\r\n                                id=\"email\"\r\n                                name=\"email\"\r\n                                v-model=\"contactForm.email\"\r\n                                placeholder=\"Enter Your Email Address\"\r\n                                required\r\n                            />\r\n                            <span\r\n                                v-if=\"errors && errors.email\"\r\n                                class=\"errMsg\"\r\n                                >{{ errors.email[0] }}</span\r\n                            >\r\n                        </label>\r\n                        <label for=\"message\">\r\n                            <textarea\r\n                                name=\"message\"\r\n                                id=\"message\"\