om/Happy-Wed-102589325754190/\"\r\n                            ><i class=\"fab fa-facebook-f\"></i\r\n                        ></a>\r\n                        <a href=\"https://twitter.com/HappyWed2?s=09\"\r\n                            ><i class=\"fab fa-twitter\"></i\r\n                        ></a>\r\n                        <a\r\n                            href=\"https://youtube.com/channel/UCcBPjhZHz_jOb1M_Nvk0mNA\"\r\n                            ><i class=\"fab fa-youtube\"></i\r\n                        ></a>\r\n                        <a href=\"\"><i class=\"fab fa-instagram\"></i></a>\r\n                    </div>\r\n                </div> -->\r\n            </div>\r\n        </div>\r\n        <flashMessage :message=\"message\"></flashMessage>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport flashMessage from \"../FlashMessage.vue\";\r\n\r\nexport default {\r\n    components: {\r\n        flashMessage,\r\n    },\r\n    data() {\r\n        return {\r\n            contactForm: {\r\n                name: \"\",\r\n                email: \"\",\r\n                message: \"\",\r\n            },\r\n            message: null,\r\n            errors: null,\r\n        };\r\n    },\r\n    methods: {\r\n        contactSubmit(e) {\r\n            let _this = this;\r\n            let formData = new FormData();\r\n            let data = JSON.stringify(_this.contactForm);\r\n            formData.append(\"data\", data);\r\n            axios\r\n                .post(\"/api/contactus\", formData)\r\n                .then(function (response) {\r\n                    _this.message = \"Query Submitted!!\";\r\n                    _this.errors = null;\r\n                    setTimeout(function () {\r\n                        _this.message = null;\r\n                    }, 3000);\r\n                    _this.contactForm.name = \"\";\r\n                    _this.contactForm.email = \"\";\r\n                    _this.contactForm.message = \"\";\r\n                })\r\n                .catch((error) => {\r\n                    _this.errors = error.response.data.errors;\r\n                    //console.log(error.response.data.errors);\r\n                    _this.message = \"Something went wrong!!\";\r\n                    setTimeout(function () {\r\n                        _this.message = null;\r\n                    }, 3000);\r\n                });\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.errMsg {\r\n    color: red;\r\n    font-size: 10px;\r\n}\r\n.links {\r\n    width: 2%;\r\n}\r\n.links > div {\r\n    position: absolute;\r\n    background: #461952;\r\n    border-radius: 3px 3px 20px 0px;\r\n    bottom: 0;\r\n    display: flex;\r\n    flex-direction: column;\r\n    padding: 1em;\r\n    right: -3%;\r\n}\r\n.links > div > a {\r\n    text-decoration: none;\r\n    color: white;\r\n}\r\n.links > div > a > i {\r\n    margin-bottom: 1.5em;\r\n    color: white;\r\n}\r\n.back-img-cont {\r\n    position: relative;\r\n}\r\n.back-img-cont > img:nth-child(1) {\r\n    width: 100%;\r\n    object-fit: cover;\r\n}\r\n.back-img-cont > img:nth-child(2) {\r\n    position: absolute;\r\n    left: 5%;\r\n    bottom: -2%;\r\n}\r\n.back-img-cont > img:nth-child(3) {\r\n    position: absolute;\r\n    right: 0%;\r\n    bottom: 20%;\r\n}\r\n.about-form {\r\n    background: #ffffff;\r\n    border-radius: 20px;\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: space-between;\r\n    flex-direction: row-reverse;\r\n    position: absolute;\r\n    top: 5%;\r\n    left: 15.5%;\r\n    padding: 3em 4em;\r\n    width: 70%;\r\n}\r\n#right-section > div {\r\n    margin-bottom: 1em;\r\n}\r\n#right-section > div > span {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 800;\r\n    font-size: 12px;\r\n    line-height: 18px;\r\n\r\n    color: #000000;\r\n}\r\n#right-section > div > img {\r\n    margin-right: 1.2em;\r\n}\r\n.contact-form {\r\n    padding: 2em;\r\n}\r\n.contact-form > h1 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 900;\r\n    font-size: 20.8938px;\r\n    line-height: 31px;\r\n    color: #461952;\r\n}\r\n.contact-form > h3 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    font-size: 14px;\r\n    line-height: 21px;\r\n    color: #000000;\r\n}\r\n.contact-form label {\r\n    width: 100%;\r\n    margin-bottom: 1em;\r\n}\r\n.contact-form input,\r\ntextarea {\r\n    width: 100%;\r\n    background-color: #f5f4fa !important;\r\n    border-radius: 3px;\r\n    border: none;\r\n    padding: 1em;\r\n}\r\n.contact-form input::placeholder,\r\ntextarea::placeholder {\r\n    font-family: \"Poppins\" !important;\r\n    font-style: normal !important;\r\n    font-weight: 900 !important;\r\n    font-size: 9.33333px I !important;\r\n    line-height: 14px !important;\r\n    color: #a8a8a8 !important;\r\n}\r\n.contact-form button {\r\n    background: #461952;\r\n    border-radius: 3px;\r\n    border: none;\r\n    outline: none;\r\n    text-align: center;\r\n    width: 100%;\r\n    padding: 1em;\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 700;\r\n    font-size: 12.33333px;\r\n    line-height: 14px;\r\n    color: #ffffff;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
